.Calendar {
  display: grid;
  max-width: 600px;
  width: 100%;
  grid-template-rows: auto 1fr;
  margin: 10px 10px;
  background-color: white;
  -webkit-box-shadow: 0 3px 4px gray;
          box-shadow: 0 3px 4px gray;
}

.Header {
  grid-row: 1/2;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.MYBtns {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  width: 100%;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.MonthYear {
  display: -ms-flexbox;
  display: flex;
  font-size: 17pt;
  width: 100%;
  margin-left: 25px;
  -ms-flex-pack: left;
      justify-content: left;
  -ms-flex-line-pack: center;
      align-content: center;
}

.Month {
  margin-right: 8px;
}

.MonthButtonsContainer {
  display: inline-block;
  padding: 0 10px;
}

.MonthButtons {
  display: block;
}

.DayNames {
  display: -ms-flexbox;
  display: flex;
  height: 35px;
  width: 100%;
  -ms-flex-flow: row;
      flex-flow: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
}

.Days {
  grid-row: 3/4;
  display: grid;
  height: 100%;
  width: 100%;
  grid-template-columns: repeat(7, 1fr);
  grid-template-rows: repeat(6, 50px);
  -ms-flex-align: center;
      align-items: center;
  justify-items: center;
}


